.sw-btn-group {
	display: none;
	.btn {
		position: absolute;
		top: 0;
		padding: 0;
		text-indent: -62499.9375rem;
		font-size: 0;
		border: 0;
		background-color: #fff;
		border: 0.125rem solid #00488b;
		border-radius: 50%;
		width: 1.875rem;
		height: 1.875rem;
		min-width: 1.875rem;
		z-index: 95;
		@include max-sm {
			width: 1.5rem;
			height: 1.5rem;
			line-height: 1.5rem;
			min-width: 1.5rem;
		}
		&:hover {
			background-color: transparent;
		}
		&::before {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 0;
			height: 0;
			border-style: solid;
			content: '';
		}
		&.sw-btn-prev {
			left: 0;
			&::before {
				margin-left: -0.125rem;
				border-width: 0.5rem 0.625rem 0.5rem 0;
				border-color: transparent #00488b transparent transparent;
			}
		}
		&.sw-btn-next {
			right: 0;
			&::before {
				margin-left: 0.125rem;
				border-width: 0.5rem 0 0.5rem 0.625rem;
				border-color: transparent transparent transparent #00488b;
			}
		}
	}
}


