.accept {
    position: relative;
    padding: 0 0 0 3.125rem;
    cursor: pointer;
    @include max-lg {
			padding-left: 4.375rem;
    }
    @include max-sm {
			padding-left: 5rem;
    }
}

.radio + .checkmark {
	position: absolute;
	top: 0.3125rem;
	left: 0;
	width: 2.125rem;
	height: 2.125rem;
	border: 0.0625rem solid #CDD1DA;
	border-radius: 50%;
	background: #FFF;
	@include max-lg {
	  width: 2.5rem;
	  height: 2.5rem;
	  top: 0.1875rem;
	}
	@include max-sm {
	  width: 3.125rem;
	  height: 3.125rem;
	  top: 0.625rem;
	}
	&::after {
    content: '';
    position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
    width: 1.25rem;
    height: 1.25rem;
    border-radius: 50%;
    background: #A7D0B1;
    opacity: 0;
    transition: .2s;
		@include max-lg {
	    width: 1.625rem;
	    height: 1.625rem;
		}
		// @include max-sm {
	 //    width: 1.875rem;
	 //    height: 1.875rem;
		// }
	}
}

.radio:checked + .checkmark::after {
    opacity: 1;
}