.jq-checkbox,
.jq-radio {
	vertical-align: -0.25rem;
	width: 1rem;
	height: 1rem;
	margin: 0 0.25rem 0 0;
	border: 0.0625rem solid #C3C3C3;
	background: linear-gradient(#FFF, #E6E6E6);
	box-shadow: 0 0.0625rem 0.0625rem rgba(0,0,0,.05), inset -0.0625rem -0.0625rem #FFF, inset 0.0625rem -0.0625rem #FFF;
	cursor: pointer;
}
.jq-checkbox.focused,
.jq-radio.focused {
	border: 0.0625rem solid #08C;
}
.jq-checkbox.disabled,
.jq-radio.disabled {
	opacity: .55;
}
.jq-checkbox {
	border-radius: 0.1875rem;
}
.jq-checkbox.checked .jq-checkbox__div {
	width: 0.5rem;
	height: 0.25rem;
	margin: 0.1875rem 0 0 0.1875rem;
	border-bottom: 0.125rem solid #666;
	border-left: 0.125rem solid #666;
	-webkit-transform: rotate(-50deg);
	transform: rotate(-50deg);
}
.jq-radio {
	border-radius: 50%;
}
.jq-radio.checked .jq-radio__div {
	width: 0.625rem;
	height: 0.625rem;
	margin: 0.1875rem 0 0 0.1875rem;
	border-radius: 50%;
	background: #777;
	box-shadow: inset 0.0625rem 0.0625rem 0.0625rem rgba(0,0,0,.7);
}
.jq-file {
	width: 16.875rem;
	border-radius: 0.25rem;
	box-shadow: 0 0.0625rem 0.125rem rgba(0,0,0,.1);
}
.jq-file input {
	cursor: pointer;
}
.jq-file__name {
	box-sizing: border-box;
	width: 100%;
	height: 2.125rem;
	padding: 0 5rem 0 0.625rem;
	border: 0.0625rem solid #CCC;
	border-bottom-color: #B3B3B3;
	border-radius: 0.25rem;
	background: #FFF;
	box-shadow: inset 0.0625rem 0.0625rem #F1F1F1;
	font: 0.875rem/2rem Arial, sans-serif;
	color: #333;
}
.jq-file__browse {
	position: absolute;
	top: 0.0625rem;
	right: 0.0625rem;
	padding: 0 0.625rem;
	border-left: 0.0625rem solid #CCC;
	border-radius: 0 0.25rem 0.25rem 0;
	background: linear-gradient(#FFF, #E6E6E6);
	box-shadow: inset 0.0625rem -0.0625rem #F1F1F1, inset -0.0625rem 0 #F1F1F1, 0 0.0625rem 0.125rem rgba(0,0,0,.1);
	font: 0.875rem/2rem Arial, sans-serif;
	color: #333;
	text-shadow: 0.0625rem 0.0625rem #FFF;
}
.jq-file:hover .jq-file__browse {
	background: linear-gradient(#F6F6F6, #E6E6E6);
}
.jq-file:active .jq-file__browse {
	background: #F5F5F5;
	box-shadow: inset 0.0625rem 0.0625rem 0.1875rem #DDD;
}
.jq-file.focused .jq-file__name {
	border: 0.0625rem solid #5794BF;
}
.jq-file.disabled,
.jq-file.disabled .jq-file__name,
.jq-file.disabled .jq-file__browse {
	border-color: #CCC;
	background: #F5F5F5;
	box-shadow: none;
	color: #888;
}
.jq-number {
	position: relative;
	vertical-align: middle;
	padding: 0 2.25rem 0 0;
}
.jq-number__field {
	width: 6.25rem;
	border: 0.0625rem solid #CCC;
	border-bottom-color: #B3B3B3;
	border-radius: 0.25rem;
	box-shadow: inset 0.0625rem 0.0625rem #F1F1F1, 0 0.0625rem 0.125rem rgba(0,0,0,.1);
}
.jq-number__field:hover {
	border-color: #B3B3B3;
}
.jq-number__field input {
	box-sizing: border-box;
	width: 100%;
	padding: 0.5rem 0.5625rem;
	border: none;
	outline: none;
	background: none;
	font: 0.875rem Arial, sans-serif;
	color: #333;
}
.jq-number__spin {
	position: absolute;
	top: 0;
	right: 0;
	width: 2rem;
	height: 0.875rem;
	border: 0.0625rem solid #CCC;
	border-bottom-color: #B3B3B3;
	border-radius: 0.25rem;
	background: linear-gradient(#FFF, #E6E6E6);
	box-shadow: 0 0.0625rem 0.125rem rgba(0,0,0,.1);
	box-shadow: inset 0.0625rem -0.0625rem #F1F1F1, inset -0.0625rem 0 #F1F1F1, 0 0.0625rem 0.125rem rgba(0,0,0,.1);
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	text-shadow: 0.0625rem 0.0625rem #FFF;
	cursor: pointer;
}
.jq-number__spin.minus {
	top: auto;
	bottom: 0;
}
.jq-number__spin:hover {
	background: linear-gradient(#F6F6F6, #E6E6E6);
}
.jq-number__spin:active {
	background: #F5F5F5;
	box-shadow: inset 0.0625rem 0.0625rem 0.1875rem #DDD;
}
.jq-number__spin:after {
	content: '';
	position: absolute;
	top: 0.25rem;
	left: 0.6875rem;
	width: 0;
	height: 0;
	border-right: 0.3125rem solid transparent;
	border-bottom: 0.3125rem solid #999;
	border-left: 0.3125rem solid transparent;
}
.jq-number__spin.minus:after {
	top: 0.3125rem;
	border-top: 0.3125rem solid #999;
	border-right: 0.3125rem solid transparent;
	border-bottom: none;
	border-left: 0.3125rem solid transparent;
}
.jq-number__spin.minus:hover:after {
	border-top-color: #000;
}
.jq-number__spin.plus:hover:after {
	border-bottom-color: #000;
}
.jq-number.focused .jq-number__field {
	border: 0.0625rem solid #5794BF;
}
.jq-number.disabled .jq-number__field,
.jq-number.disabled .jq-number__spin {
	border-color: #CCC;
	background: #F5F5F5;
	box-shadow: none;
	color: #888;
}
.jq-number.disabled .jq-number__spin:after {
	border-bottom-color: #AAA;
}
.jq-number.disabled .jq-number__spin.minus:after {
	border-top-color: #AAA;
}
.jq-selectbox {
	vertical-align: middle;
	cursor: pointer;
}
.jq-selectbox__select {
	height: 2rem;
	padding: 0 2.8125rem 0 0.625rem;
	border: 0.0625rem solid #CCC;
	border-bottom-color: #B3B3B3;
	border-radius: 0.25rem;
	background: linear-gradient(#FFF, #E6E6E6);
	box-shadow: inset 0.0625rem -0.0625rem #F1F1F1, inset -0.0625rem 0 #F1F1F1, 0 0.0625rem 0.125rem rgba(0,0,0,.1);
	font: 0.875rem/2rem Arial, sans-serif;
	color: #333;
	text-shadow: 0.0625rem 0.0625rem #FFF;
}
.jq-selectbox__select:hover {
	background: linear-gradient(#F6F6F6, #E6E6E6);
}
.jq-selectbox__select:active {
	background: #F5F5F5;
	box-shadow: inset 0.0625rem 0.0625rem 0.1875rem #DDD;
}
.jq-selectbox.focused .jq-selectbox__select {
	border: 0.0625rem solid #5794BF;
}
.jq-selectbox.disabled .jq-selectbox__select {
	border-color: #CCC;
	background: #F5F5F5;
	box-shadow: none;
	color: #888;
}
.jq-selectbox__select-text {
	display: block;
	width: 100%;
}
.jq-selectbox .placeholder {
	color: #888;
}
.jq-selectbox__trigger {
	position: absolute;
	top: 0;
	right: 0;
	width: 2.125rem;
	height: 100%;
	border-left: 0.0625rem solid #CCC;
}
.jq-selectbox__trigger-arrow {
	position: absolute;
	top: 0.875rem;
	right: 0.75rem;
	width: 0;
	height: 0;
	border-top: 0.3125rem solid #999;
	border-right: 0.3125rem solid transparent;
	border-left: 0.3125rem solid transparent;
}
.jq-selectbox:hover .jq-selectbox__trigger-arrow {
	border-top-color: #000;
}
.jq-selectbox.disabled .jq-selectbox__trigger-arrow {
	border-top-color: #AAA;
}
.jq-selectbox__dropdown {
	box-sizing: border-box;
	width: 100%;
	margin: 0.125rem 0 0;
	padding: 0;
	border: 0.0625rem solid #CCC;
	border-radius: 0.25rem;
	background: #FFF;
	box-shadow: 0 0.125rem 0.625rem rgba(0,0,0,.2);
	font: 0.875rem/1.125rem Arial, sans-serif;
}
.jq-selectbox__search {
	margin: 0.3125rem;
}
.jq-selectbox__search input {
	box-sizing: border-box;
	width: 100%;
	margin: 0;
	padding: 0.3125rem 1.6875rem 0.375rem 0.5rem;
	border: 0.0625rem solid #CCC;
	border-radius: 0.1875rem;
	outline: none;
	background: url('data:image/png;base64,') no-repeat 100% 50%;
	box-shadow: inset 0.0625rem 0.0625rem #F1F1F1;
	color: #333;
}
.jq-selectbox__not-found {
	margin: 0.3125rem;
	padding: 0.3125rem 0.5rem 0.375rem;
	background: #F0F0F0;
	font-size: 0.8125rem;
}
.jq-selectbox ul {
	margin: 0;
	padding: 0;
}
.jq-selectbox li {
	min-height: 1.125rem;
	padding: 0.3125rem 0.625rem 0.375rem;
	color: #231F20;
}
.jq-selectbox li.selected {
	background-color: #A3ABB1;
	color: #FFF;
}
.jq-selectbox li:hover {
	background-color: #08C;
	color: #FFF;
}
.jq-selectbox li.disabled {
	color: #AAA;
}
.jq-selectbox li.disabled:hover {
	background: none;
}
.jq-selectbox li.optgroup {
	font-weight: bold;
}
.jq-selectbox li.optgroup:hover {
	background: none;
	color: #231F20;
	cursor: default;
}
.jq-selectbox li.option {
	padding-left: 1.5625rem;
}
.jq-select-multiple {
	box-sizing: border-box;
	padding: 0.0625rem;
	border: 0.0625rem solid #CCC;
	border-bottom-color: #B3B3B3;
	border-radius: 0.25rem;
	background: #FFF;
	box-shadow: inset 0.0625rem 0.0625rem #F1F1F1, 0 0.0625rem 0.125rem rgba(0,0,0,.1);
	font: 0.875rem/1.125rem Arial, sans-serif;
	color: #333;
	cursor: default;
}
.jq-select-multiple.focused {
	border: 0.0625rem solid #5794BF;
}
.jq-select-multiple.disabled {
	border-color: #CCC;
	background: #F5F5F5;
	box-shadow: none;
	color: #888;
}
.jq-select-multiple ul {
	margin: 0;
	padding: 0;
}
.jq-select-multiple li {
	padding: 0.1875rem 0.5625rem 0.25rem;
	list-style: none;
}
.jq-select-multiple li:first-child {
	border-radius: 0.1875rem 0.1875rem 0 0;
}
.jq-select-multiple li:last-child {
	border-radius: 0 0 0.1875rem 0.1875rem;
}
.jq-select-multiple li.selected {
	background: #08C;
	color: #FFF;
}
.jq-select-multiple li.disabled {
	color: #AAA;
}
.jq-select-multiple li.optgroup {
	font-weight: bold;
}
.jq-select-multiple li.option {
	padding-left: 1.5625rem;
}
.jq-select-multiple.disabled li.selected,
.jq-select-multiple li.selected.disabled {
	background: #CCC;
	color: #FFF;
}
