	border-top-color: #B3B3B3;
	outline: none;
	box-shadow: inset 0 0.0625rem 0.125rem rgba(0,0,0,.1);
}
button.styler,
input[type='button'].styler,
input[type='submit'].styler,
input[type='reset'].styler {
	overflow: visible;
	padding: 0.5rem 0.6875rem;
	border: 0.0625rem solid #CCC;
	border-bottom-color: #B3B3B3;
	border-radius: 0.25rem;
	outline: none;
	background: linear-gradient(#FFF, #E6E6E6);
	box-shadow: inset 0.0625rem -0.0625rem #F1F1F1, inset -0.0625rem 0 #F1F1F1, 0 0.0625rem 0.125rem rgba(0,0,0,.1);
	font: 0.875rem Arial, sans-serif;
	color: #333;
	text-shadow: 0.0625rem 0.0625rem #FFF;
	cursor: pointer;
}
button.styler.styler::-moz-focus-inner,
input[type='button'].styler.styler::-moz-focus-inner,
input[type='submit'].styler.styler::-moz-focus-inner,
input[type='reset'].styler.styler::-moz-focus-inner {
	padding: 0;
	border: 0;
}
button.styler:not([disabled]):hover,
input[type='button'].styler:not([disabled]):hover,
input[type='submit'].styler:not([disabled]):hover,
input[type='reset'].styler:not([disabled]):hover,
input[type='reset'].styler:hover {
	background: linear-gradient(#F6F6F6, #E6E6E6);
}
button.styler:active,
input[type='button'].styler:active,
input[type='submit'].styler:active,
input[type='reset'].styler:active {
	background: #F5F5F5;
	box-shadow: inset 0.0625rem 0.0625rem 0.1875rem #DDD;
}
button.styler[disabled],
input[type='button'].styler[disabled],
input[type='submit'].styler[disabled] {
	border-color: #CCC;
	background: #F5F5F5;
	box-shadow: none;
	color: #888;
}