@import url('https://fonts.googleapis.com/css?family=PT+Sans:400,700&display=swap');


@font-face {
    font-family: 'MuseoSansRounded300';
    src: url('../fonts/Museo/MuseoSansRounded-300.eot');
    src: url('../fonts/Museo/MuseoSansRounded-300.eot') format('embedded-opentype'),
         url('../fonts/Museo/MuseoSansRounded-300.woff2') format('woff2'),
         url('../fonts/Museo/MuseoSansRounded-300.woff') format('woff'),
         url('../fonts/Museo/MuseoSansRounded-300.ttf') format('truetype'),
         url('../fonts/Museo/MuseoSansRounded-300.svg#MuseoSansRounded-300') format('svg');
}

