  // Media max-width

    // @include max-m
    $max-m-width: 20rem;
    // @include max-xs
    $max-xs-width: 30rem;
    // @include max-sm
    $max-sm-width: 35.9375rem;
    // @include max-md
    $max-md-width: 47.9375rem;
    // @include max-lg
    $max-lg-width: 61.9375rem;
    // @include max-xl
    $max-xl-width: 74.9375rem;
    // @include max-1300
    $max-1300-width: 81.25rem;
    // @include max-1400
    $max-1400-width: 87.5rem;
    // @include max-1500
    $max-1500-width: 93.75rem;
    // @include max-1600
    $max-1600-width: 100rem;
    // @include max-1700
    $max-1700-width: 106.25rem;

    @mixin max-1700 {
      @media (max-width: #{$max-1700-width}) {
        @content;
      }
    } 
