        @content;
      }
    }
    @mixin max-xl {
      @media (max-width: #{$max-xl-width}) {
        @content;
      }
    }
    @mixin max-lg {
      @media (max-width: #{$max-lg-width}) {
        @content;
      }
    }
    @mixin max-md {
      @media (max-width: #{$max-md-width}) {
        @content;
      }
